.phonebookContainer {
  display: flex;
  justify-content: space-around;
  padding: 2rem;
}

.titleContactList {
  font-size: 3.5rem;
  margin-bottom: 2rem;
  margin-left: 5%;
}

.wrapperContactsList {
  width: 50%;
  margin-left: 10rem;
}

.contactCard {
  margin: 1rem 0rem 3rem 3rem;
  padding: 1rem 0.5rem;
  border: 1px solid #000;
  position: relative;
  width: 75%;
  transition: 0.3s;

  &:hover {
    -webkit-box-shadow: 13px 12px 26px 17px rgba(34, 60, 80, 0.31);
    -moz-box-shadow: 13px 12px 26px 17px rgba(34, 60, 80, 0.31);
    box-shadow: 13px 12px 26px 17px rgba(34, 60, 80, 0.31);
  }
}

.titleNameCard {
  font-size: 2.5rem;
  padding-left: 1rem;
}

.numberContactText {
  padding-left: 1.5rem;
  font-size: 2rem;
}

.js-btn-deleteContact {
  position: absolute;
  right: 2%;
  top: 30%;

  text-align: center;

  font-size: 1.5rem;
  width: 4rem;
  height: 2.3rem;
  border: none;
  border-bottom: 5px solid #333;
  border-radius: 5px;
  cursor: pointer;
  background-color: rgb(214, 0, 0);
  font-weight: bold;
  transition: 0.3s;

  &:hover {
    background-color: red;
  }

  &:active {
    border-bottom: none;
  }
}

.titleNoContacts {
  font-size: 1.8rem;
  line-height: 145%;
}

.subtitleFormAddContact {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.labelInputFormContact {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.titleInputContact {
  font-size: 2.5rem;
}

.js-btn-addContact {
  margin-top: 2rem;
  margin-left: 3rem;
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 5px;
  background-color: cadetblue;

  border-bottom: 5px solid #000;
  border-left: 5px solid #000;

  transition: 0.2s;
  cursor: pointer;

  -webkit-box-shadow: -2px 4px 48px 0px rgba(34, 60, 80, 0.33);
  -moz-box-shadow: -2px 4px 48px 0px rgba(34, 60, 80, 0.33);
  box-shadow: -2px 4px 48px 0px rgba(34, 60, 80, 0.33);

  &:hover {
    background-color: rgb(55, 211, 216);
  }

  &:active {
    border-bottom: 0px solid #000;
    border-left: 0px solid #000;
    box-shadow: 0px;
  }
}

.wrapperInputFilter {
  margin-left: 4.5%;
  width: 70%;

  margin-top: 2rem;
  margin-bottom: 2.5rem;
}

.js-inputFilter {
  padding: 0.5rem 1rem;
  border-radius: 4px;
  border: 1px solid #000;
  outline: none;
}

.colorWrapper {
  width: 2rem;
  height: 2rem;
  border: 1px solid #000;
}

.itemValidateColor {
  display: flex;
  align-items: center;
  margin: 1rem;
}

.subtitleColorsScheme {
  font-size: 2rem;
}

.descriptionColorInfo {
  font-size: 1.6rem;
  padding-left: 1.5rem;
}

.colorGood {
  background-color: rgb(15, 223, 15);
}

.colorMedium {
  background-color: rgb(205, 5, 255);
}

.colorBad {
  background-color: red;
}

.inputForm {
  padding: 0.7rem 1rem;
  font-size: 1.7rem;
  margin: 1rem;
  border: 1px solid #333;
  border-radius: 4px;
  outline: none;
  transition: 0.2s all ease;

  &:focus {
    border-color: rgb(15, 223, 15);

    background-color: rgb(200, 206, 200);
  }

  &::placeholder {
    color: #000;
  }
}

.inputFormBad {
  // border-color: red;

  &:focus {
    border-color: red;
  }
}

.inputFormNotUniq {
  border-color: rgb(205, 5, 255);

  &:focus {
    border-color: rgb(205, 5, 255);
  }
}
